.app__about {
  display: flex;
  justify-content: center;
  position: relative;
  padding: 80px 30px;

  @media (max-width: 960px) {
    padding: 66px 16px;
  }

  @media (max-width: 640) {
    padding: 32px 16px;
  }

  z-index: 1;
}

.app_about-container {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1100px;

  @media (max-width: 960px) {
    flex-direction: column;
  }
}

.app__about-left {
  width: 100%;
  order: 1;

  @media (max-width: 960px) {
    order: 2;
    margin-bottom: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  @media (max-width: 640px) {
    order: 2;
    margin-bottom: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  a {

    color: black;
    appearance: button;
    text-decoration: none;
    width: 95%;
    max-width: 300px;
    text-align: center;
    padding: 16px 0;
    border-radius: 20px;
    cursor: pointer;
    font-size: 20px;
    font-weight: 600;
    transition: all 0.2s ease-in-out !important;

    &:hover {
      transform: scale(1.05);
      transition: all 0.4s ease-in-out;

    }


    @media (max-width: 640px) {
      padding: 12px 0;
      font-size: 18px;
    }
  }


}

.app__about-title {
  font-weight: 700;
  font-size: 50px;
  line-height: 68px;

  @media (max-width: 960px) {
    text-align: center;
  }

  @media (max-width: 640px) {
    font-size: 40px;
    line-height: 48px;
    margin-bottom: 8px;
  }
}

.app__about-textloop {
  font-weight: 600;
  font-size: 32px;
  display: flex;
  gap: 12px;
  line-height: 68px;

  @media (max-width: 960px) {
    text-align: center;
  }

  @media (max-width: 640px) {
    font-size: 22px;
    line-height: 48px;
    margin-bottom: 16px;
  }

  @media (max-width: 400px) {
    flex-direction: column;
    line-height: 30px;

  }
}

.app_about-subtitle {
  font-size: 20px;
  line-height: 32px;
  margin-bottom: 42px;

  @media (max-width: 960px) {
    text-align: center;
  }

  @media (max-width: 640px) {
    font-size: 16px;
    line-height: 32px;
  }
}


.app__about-right {
  width: 100%;
  display: flex;
  order: 2;
  justify-content: flex-end;
  gap: 12px;

  @media (max-width: 960px) {
    order: 1;
    justify-content: center;
    align-items: center;
    margin-bottom: 80px;
  }

  @media (max-width: 640px) {
    margin-bottom: 30px;
  }

  img {
    position: relative;
    width: 100%;
    height: 100%;
    max-width: 400px;
    max-height: 400px;
    border-radius: 50%;

    @media (max-width: 768px) {
      max-width: 400px;
      max-height: 400px;
    }

    @media (max-width: 640px) {
      max-width: 280px;
      max-height: 280px;
    }
  }



}